
.header {
  background-color: #232457;
  font-size: .2rem;
  font-weight: bold;
  height: 0.46rem;
  line-height: 0.46rem;
  text-align: left;
  width: 100%;
  position: relative;
}
.header .show-side-bar {
    display: inline-block;
    font-size: 14px;
    font-weight: normal;
    padding-left: 10px;
}
.header .back {
    display: inline-block;
    height: 100%;
    width: 60px;
    padding-left: 10px;
}
.header .title {
    display: inline-block;
    width: 1rem;
    height: 0.46rem;
    line-height: 0.46rem;
    text-align: center;
    position: absolute;
    left: 50%;
    margin-left: -.5rem;
}



