
html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-y: scroll;
}
body {
  background-color: #FFF;
  color: #FFF;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#app {
  width: 172%;
  height: 100%;
  transition: left .4s;
  left: -72%;
  position: fixed;
}
#app .side-bar {
    display: inline-block;
    height: 100%;
    vertical-align: top;
    width: 41.86%;
    position: relative;
}
#app .container {
    display: inline-block;
    height: 100%;
    vertical-align: top;
    width: 58.14%;
    margin-left: -5px;
    position: relative;
}
.showSideBar {
  left: 0 !important;
}



