
.footer {
  background-color: #232457;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.5rem;
  width: 100%;
}
.footer ul {
    height: .48rem;
    list-style: none;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.footer ul li {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      height: 100%;
      text-align: center;
      overflow: hidden;
      position: relative;
      padding-top: 5px;
}
.footer ul li img {
        width: 22px;
        transition: all 1s;
}
.footer ul li p {
        color: #727394;
        font-size: 12px;
        text-align: center;
        width: 100%;
        transition: all 1s;
}
.footer ul li .router-link-active p {
        color: #FFF;
}
.footer ul li:active > div img {
        -webkit-transform: scale(2);
                transform: scale(2);
}
.footer ul li:active > div p {
        font-size: 18px;
        -webkit-transform: scale(2);
                transform: scale(2);
}



